	module.exports = {"data":{"challengeNode":{"title":"Import a Default Export","description":["在上一个挑战里，你学会了<code>export default</code>的用法。还有一个重要的点，你可能需要另外一种<code>import</code>的语法来导入默认导出。","在下面的例子里有一个<code>add</code>函数, 它在<code>\"math_functions\"</code>文件里默认被导出。让我们看看来如何导入它：","<blockquote>import add from \"math_functions\";<br>add(5,4); //将会返回 9</blockquote>","这个语法只有一处不同的地方 —— 被导入的<code>add</code>值，并没有被花括号<code>{}</code>所包围。与导出值的方法不同，导入默认导出的写法仅仅只是简单的讲变量名写在<code>import</code>之后。","<hr>","在下面的代码中，请导入在同目录下的<code>\"math_functions\"</code>文件中默认导出的<code>subtract</code>值。"],"challengeType":1,"fields":{"slug":"/javascript-algorithms-and-data-structures/es6/import-a-default-export","blockName":"ES6","tests":[{"text":"正确导入<code>export default</code>方法导出的值。","testString":"getUserInput => assert(getUserInput('index').match(/import\\s+subtract\\s+from\\s+\"math_functions\"/g), '正确导入<code>export default</code>方法导出的值。');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"\"use strict\";\nsubtract(7,4);","head":"window.require = function(str) {\nif (str === 'math_functions') {\nreturn function(a, b) {\nreturn a - b;\n}}};","tail":""},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"/javascript-algorithms-and-data-structures/regular-expressions","template":null,"required":[],"nextChallengePath":"/javascript-algorithms-and-data-structures/regular-expressions/using-the-test-method","id":"587d7b8d367417b2b2512b59"},"slug":"/javascript-algorithms-and-data-structures/es6/import-a-default-export"}}

